<script>
import test from "./components/test.vue";
export default {
  components: {
    test
  },
  mounted() {
    this.$nextTick(() => {
      this.runder();
      addEventListener("scroll", this.runder);
    });
  },
  methods: {
    runder() {
      let top =
        pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      let vh = document.documentElement.clientHeight;
      let doms = document.querySelectorAll(".animate__animated");
      doms.forEach(dom => {
        if (top + vh - 50 > dom.offsetTop) {
          dom.classList.add(dom.dataset.animate);
          dom.style.opacity = 1;
        } else {
          dom.classList.remove(dom.dataset.animate);
          dom.style.opacity = 0;
        }
      });
    }
  }
};
</script>

